\n\u003ch3 id=\"makefile----\"\u003eMakefile    \u003c/h3\u003e\n\u003cp\u003eMakefile shell script    . , Makefile      .                  .        .   Makefile  .  Makefile   make    . (20     ,    ..)\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eThe purpose of the \u003cem\u003emake\u003c/em\u003e utility is to determine automatically which pieces of a large program need to be recompiled, and issue the commands to recompile them.\u003c/strong\u003e \u003ca href=\"https://wiki.kldp.org/KoreanDoc/html/GNU-Make/GNU-Make-1.html\"\u003e(Reference1)\u003c/a\u003e\u003c/p\u003e\n\u003cp\u003e\u003ccode\u003enpm run serve\u003c/code\u003e Makefile    ,  \u003ccode\u003enpm run serve\u003c/code\u003e              .\u003cbr /\u003e\u003cbr\u003e , terrace  Makefile .\u003c/p\u003e\n\u003cpre class=\"bash\"\u003e\u003ccode\u003evi Makefile\u003c/code\u003e\u003c/pre\u003e\n\u003cpre class=\"makefile\"\u003e\u003ccode\u003erun:\n    ./bin/terrace\n\nbuild:\n    go build -x -o ./bin/terrace ./src\n    sudo setcap CAP_NET_BIND_SERVICE=+eip ./bin/terrace\u003c/code\u003e\u003c/pre\u003e\n\u003cp\u003e Makefile  \u003ccode\u003emake run\u003c/code\u003e   \u003ccode\u003e./bin/terrace\u003c/code\u003e , \u003ccode\u003emake build\u003c/code\u003e      .    \u003ccode\u003emake\u003c/code\u003e     \u003ccode\u003emake run\u003c/code\u003e .       Makefile     . Makefile    TAB      TAB      .  ./bin/terrace   build:  TAB  .\u003cbr /\u003e\u003cbr\u003e\u003c/p\u003e\n\u003cpre class=\"makefile\"\u003e\u003ccode\u003ego build -x -o ./bin/terrace ./src\u003c/code\u003e\u003c/pre\u003e\n\u003cp\u003e  -x build      . -o output . build    ./bin/terrace  , ./src src   go  (    main.go)  .\u003c/p\u003e\n\u003cp\u003e, src  main.go  ./bin/terrace   .  \u003ccode\u003emake run\u003c/code\u003e  \u003ccode\u003emake build\u003c/code\u003e  .     sudo   ./bin/terrace   \u003ccode\u003esudo setcap CAP_NET_BIND_SERVICE=+eip ./bin/terrace\u003c/code\u003e   .\u003c/p\u003e\n\u003ch3 id=\"