st (\n  WebRoot = \u0026quot;./web_root/\u0026quot;\n)\n\nfunc rootHandler(w http.ResponseWriter, r *http.Request) {\n    // when request is root, send index.html\n    // otherwise, send the file\n\n    log.Println(r.URL.Path)\n\n    path := r.URL.Path[len(\u0026quot;/\u0026quot;):]\n\n    source, err := ioutil.ReadFile(WebRoot + path)\n    if err != nil {\n        source, err = ioutil.ReadFile(WebRoot + path + \u0026quot;/index.html\u0026quot;)\n        if err != nil {\n            // Redirect to 404 page\n            w.WriteHeader(http.StatusNotFound)\n            fmt.Fprint(w, err)\n            log.Println(\u0026quot;(rootHandler) \u0026quot;, err)\n            return\n        }\n    }\n\n    // Set content type as css if required file\u0026#39;s extension is css\n    if len(path) \u0026gt;= 4 \u0026amp;\u0026amp; path[len(path)-4:] == \u0026quot;.css\u0026quot; {\n        w.Header().Set(\u0026quot;Content-Type\u0026quot;, \u0026quot;text/css\u0026quot;)\n    }\n\n    fmt.Fprint(w, string(source))\n    log.Println(\u0026quot;(rootHandler) The requested file has been sent: \u0026quot;, WebRoot+path)\n}\n\nfunc main() {\n    http.HandleFunc(\u0026quot;/\u0026quot;, rootHandler)\n    log.Fatal(http.ListenAndServe(\u0026quot;:80\u0026quot;, nil))\n}\u003c/code\u003e\u003c/pre\u003e\n\u003cp\u003emain.go  .  , \u003ccode\u003er.URL.Path\u003c/code\u003e = \u0026quot;/bookThinkBlog/2018/12/28/book-sapiens/\u0026quot; . r  request (   )  .\u003c/p\u003e\n\u003cp\u003e\u003ccode\u003epath := r.URL.Path[len(\u0026quot;/\u0026quot;):]\u003c/code\u003e    \u003ccode\u003epath\u003c/code\u003e= \u0026quot;bookThinkBlog/2018/12/28/book-sapiens/\u0026quot;    \u0026quot;/\u0026quot;  .\u003c/p\u003e\n\u003cp\u003e\u003ccode\u003esource\u003c/code\u003e= \u0026quot;./web_root/bookThinkBlog/2018/12/28/book-sapiens/\u0026quot;   . err      , err  \u003ccode\u003esource\u003c/code\u003e=\u0026quot;./web_root/bookThinkBlog/2018/12/28/book-sapiens/index.html\u0026quot;  .  err  404(